<template>
  <div class="main-menu-content">
    <horizontal-nav-menu-items :items="items" />
  </div>
</template>

<script>
import navMenuItems from '@/navigation/main'
import HorizontalNavMenuItems from './components/horizontal-nav-menu-items/HorizontalNavMenuItems.vue'

export default {
  components: {
    HorizontalNavMenuItems,
  },
  data() {
    return {
      items: [],
    }
  },
  setup() {
    return {
      navMenuItems,
    }
  },
  mounted() {
    this.items = navMenuItems.map(e => {
      const { resolved } = this.$router.resolve({ name: e.route })
      return {
        ...e,
        ...resolved.meta,
      }
    })
  },
}
</script>

<style lang="scss">
@import "~@core/scss/base/core/menu/menu-types/horizontal-menu.scss";
</style>
