<template>
  <b-link href="/login">
    <feather-icon
      size="21"
      icon="LogInIcon"
    />
  </b-link>
</template>

<script>
import useAppConfig from '@core/app-config/useAppConfig'
import { computed } from '@vue/composition-api'
import { BLink } from 'bootstrap-vue'

export default {
  components: {
    BLink,
  },
  setup() {
    const { skin } = useAppConfig()

    const isDark = computed(() => skin.value === 'dark')

    return { skin, isDark }
  },
}
</script>
